<template>
  <div class="list-container">
    <nav>
      <ul>
        <li v-bind:class="curIndex == $index ? 'active': ''" v-for="item in navInfo" v-on:click="switchList($index)">{{item}}</li>
      </ul>
    </nav>
    <div class="goodList" id="list-scroll">
      <ul>
        <li v-for="item in goodList" v-link="{path: ('/detail?'+item.goodId)}">
          <!-- v-link="{path: '/index/shoppingCart?1'}" -->
          <img v-bind:src="item.imgUrl">
          <div class="info">
            <p class="goodName">{{item.name}}</p>
            <!-- <p class="song">及时送</p> -->
            <p class="less">不少于500g</p>
            <p class="goodPrice">￥{{item.price}}</p>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
// var Vue = require('../libs/vue.js');
// var VueResource = require('../libs/vue-resource.js');
// Vue.use(VueResource);

var listScroll;

import { changeIndex } from "../vuex/actions";
import { getIndex } from '../vuex/getters';
export default {
  vuex: {
    actions: {
      change: changeIndex
    },
    getters: {
      curIndex: getIndex
    }
  },
  data() {
    return {
      list: [],
      goodList:[],
      curIndex:0,
      navInfo:[
        "所有商品",
        "招牌果品",
        "新品尝鲜",
        "进口鲜果",
        "国产鲜果",
        "果篮礼盒",
        "休闲干果"
      ]
    }
  },

  ready: function() {
    var that = this;
    this.$http.get('/mock/goodInfo.json')
      .then((res) => {
        this.list = res.data;
        this.goodList=this.list;
      });
    Vue.nextTick(function(){
      setTimeout(function(){
        listScroll=new IScroll('#list-scroll', {
          click: true
        });
      },500)
    });


    this.change(1);
  },

  methods: {
    switchList:function(index){
      var that=this;
      this.curIndex=index;
      switch(index){
        case 0:
          that.goodList=that.list;
          break;
        case 1:
          that.goodList=this.changeList(1);
          break;
        case 2:
          that.goodList=this.changeList(2);
          break;
        case 3:
          that.goodList=this.changeList(3);
          break;
        case 4:
          that.goodList=this.changeList(4);
          break;
        case 5:
          that.goodList=this.changeList(5);
          break;
        case 6:
          that.goodList=this.changeList(6);
          break;
      }
      //listScroll.refresh();
    },
    changeList:function(index){
      var arr=[];
      for(var i=0;i<this.list.length;i++){
        if(index==this.list[i].goodId.substring(1,2)){
          arr.push(this.list[i]);
        }
      }
      return arr;
    }
  }

};
</script>



