<template>
  <div class="detail-box">
    <div class="detail-container" id="detail-scroll">
      <div>
        <img v-bind:src="imgUrl" alt="" class="showImg" />
        <img v-link="{path: '/index/list'}" src="/vue-project-pagoda/images/detail/icon_back_hover.png" class="back" alt="">
        <img src="/vue-project-pagoda/images/detail/icon_share.png" class="share" alt="">
        <img v-link="{path: '/index'}" src="/vue-project-pagoda/images/detail/icon_home_hover.png" class="home" alt="">
        <div class="info">
          <p class="goodName">B级-南丰蜜桔</p>
          <!-- <p class="song">及时送</p> -->
          <p class="less">不少于500g</p>
          <p class="goodPrice">￥7.3</p>
        </div>
        <div class="maiguo">
          购买过该商品的顾客也购买过
        </div>
        <ul class="tuijianBox">
          <li v-for="item in tuijian">
            <img v-bind:src="item.imgUrl" alt="" />
            <div class="info">
              <p class="goodName">B级-南丰蜜桔</p>
              <!-- <p class="song">及时送</p> -->
              <p class="less">不少于500g</p>
              <p class="goodPrice">￥7.3</p>
            </div>
          </li>
        </ul>
      </div>
    </div>
    <footer>
      <div class="shoppingIcon">
        <img src="/vue-project-pagoda/images/detail/shopping_cart.png" alt="" />
        <div class="num">2</div>
      </div>
      <div class="addTo">加入购物车</div>
    </footer>
  </div>
</template>


<script>

  var detailScroll=null;

  export default {
    data() {
      return {
        imgUrl:"",
        goodInfo:{},
        tuijian:[]
      }
    },

    ready:function(){
      var index=location.href.split("?")[1];
      this.$http.get('/mock/goodInfo.json')
        .then((res) => {
          this.list = res.data;
          for(var i=0;i<res.data.length;i++){
            if(index === res.data[i].goodId){
              this.goodInfo=res.data[i];
              this.imgUrl=res.data[i].imgUrl;
            }else if(index.substring(0,2) === res.data[i].goodId.substring(0,2)){
              this.tuijian.push(res.data[i]);
            }
          }
        });
      Vue.nextTick(function(){
        setTimeout(function(){
          detailScroll=new IScroll('#detail-scroll', {
            click: true
          });
        },500)
      });
    },

    methods:{

    }

  }
</script>



