import "../styles/usage/page/app.scss";
import "../styles/usage/page/app-list.scss";
import "../styles/usage/page/app-guide.scss";
import "../styles/usage/page/app-shoppingCart.scss";
import "../styles/usage/page/app-detail.scss";


// views
import guide from "./components/guide.vue";
import index from "./components/index.vue";
import main from "./components/main.vue";
import list from "./components/list.vue";
import shoppingCart from "./components/shoppingCart.vue";
import my from "./components/my.vue";
import detail from "./components/detail.vue";

//router
//import Vue from "./libs/vue.js";
//import VueRouter from "./libs/vue-router.js";
//Vue.use(VueRouter);

let router=new VueRouter();

import store from "./vuex/store";
//修改根组件来让应用注意到 store 的存在位置。让它的子组件和 store 连接
let App=Vue.extend({
  store: store
});

router.map({
  '/':{
    component:guide
  },
  '/index':{
    component:index,
    subRoutes:{
      '/':{
        component:main
      },
      '/list':{
        component:list
      },
      '/shoppingCart':{
        component:shoppingCart
      },
      '/my':{
        component:my
      }
    }
  },
  "detail":{
    component:detail
  }
});

router.start(App,'body');



