      }
    },
    data() {
      return {
        tablist: [
          {path: '/index', icon: '&#xe624;', name: '首页'},
          {path: '/index/list', icon: '&#xe632;', name: '品类'},
          {path: '/index/shoppingCart', icon: '&#xe63b;', name: '购物车'},
          {path: '/index/my', icon: '&#xe61a;', name: '我'}
        ]
      }
    },

    ready(){

    },


    methods: {
      changPage(i){

      }
    }
  }
</script>



