<template>
  <div class="swiper-container guide-container">
    <div class="swiper-wrapper">
      <div class="swiper-slide" v-for="item in guideImages">
        <img v-if="$index==3" v-link="{path: '/index'}" src="/vue-project-pagoda/images/guide/guide_page_button.png"  id="btn"/>
        <img v-bind:src="item" class="bigImg" />
      </div>
    </div>
  </div>
</template>


<script>
export default {
  data() {
    return {
      guideImages: [
        "/vue-project-pagoda/images/guide/guide_page_1.png",
        "/vue-project-pagoda/images/guide/guide_page_2.png",
        "/vue-project-pagoda/images/guide/guide_page_3.png",
        "/vue-project-pagoda/images/guide/guide_page_4.png"
      ]
    }
  },

  ready:function(){
    var guideSwiper = new Swiper(".guide-container", {
      autoplay: 1500,
      autoplayStopOnLast: true,
      direction : 'horizontal',
      speed:300,
      loop:false
    });
  }
}
  </script>



